/*
Import extenal libraries
*/
import React, { Component } from 'react';
import Input from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel';
import PropTypes from 'prop-types';
import TextField from "@material-ui/core/TextField";



/*
Import internal libraries
*/
import Api from '../../services';
import PostsList from '../../components/posts-list';

class OrderTicketPage extends Component {

    static propTypes ={
        classes: PropTypes.object.isRequired,
    }

    change = (name, e) => {
        e.persist();
        this.props.handleChange(e);
        this.props.setFieldTouched(name, true, false);
    }

    render() {
        const {
            values: { name, firstname, validationCode, amount, museumId },
            errors,
            touched,
            handleChange,
            handleSubmit,
            isValid,
            setFieldTouched,
            musea,
            classes
        } = this.props;
        console.log(musea);


        return (
            <form
              onSubmit={(e) => {
                this.props.handleSubmit(e);
              }}
              method="POST"
          >
                <TextField
                id="name"
                name="name"
                label="name"
                value={name}
                onChange={this.change.bind(null, "name")}
                fullWidth
        
              />
              <TextField
                id="firstname"
                name="firstname"
                label="firstname"
                fullWidth
                multiline
                rows="4"
                value={firstname}
                onChange={this.change.bind(null, "firstname")}
        
              />
        
              <TextField
                id="validationCode"
                name="validationCode"
                label="validation Code"
                fullWidth
                multiline
                rows="10"
                value={validationCode}
                onChange={this.change.bind(null, "validationCode")}
        
              />

              <TextField
                id="amount"
                name="amount"
                label="amount"
                fullWidth
                multiline
                rows="10"
                value={amount}
                onChange={this.change.bind(null, "amount")}
        
              />

              <FormControl>
                <InputLabel>Route</InputLabel>
                <Select>
                  <MenuItem>Eigen keuze</MenuItem>
                  <MenuItem>Alles</MenuItem>
                </Select>
              </FormControl>

              <FormControl>
                <InputLabel htmlFor="museumId">Museum</InputLabel>
                <Select
                  value={museumId}
                  onChange={this.change.bind(null, "museum")}
                  inputProps={{
                    name: 'museumId',
                    id: 'museumId',
                  }}
                >
                  {musea && musea.map((museum, index) => (
                    <MenuItem key={museum.id} value={museum.id}>{museum.name}</MenuItem>
                  ))}
                </Select>
              </FormControl>

        
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                disabled={!isValid}
              >
                Submit
              </Button>
                </form>

        )
    }
}

export default (OrderTicketPage);